<template>
    <div class="Account">
        <Header>
            <i slot="first" class="iconfont icon-prev" @click="$router.go(-1)"></i>
            <p slot="mid">账户信息</p>
            <div slot="last"></div>
        </Header>
        <div class="Account_head">
            <div class="Account_headone">
                <div class="Account_div">
                    <!-- 头像 -->
                    <div>头像</div>
                    <div><img :src="'//elm.cangdu.org/img/'+this.$store.state.img" /></div>
                    <div><van-icon name="arrow" /></div>
                    <input type="file" class="file" @change="shchuan()" ref="inp">
                </div>
                <!-- 用户名 -->
                <div class="Account_div" @click="usna()">
                    <div>用户名</div>
                    <div><span>{{this.$store.state.ma.username}}</span></div>
                    <div><van-icon name="arrow" /></div>
                </div>
                <!-- 收货地址 -->
                <div class="Account_div" @click="ShiAddress()">
                    <div>收货地址</div>
                    <div><van-icon name="arrow" /></div>
                </div>
            </div>
            <!-- 账户绑定 -->
            <div class="Account_binding">
                <span>账号绑定</span>
            </div>
            <!-- 收货地址 -->
            <div class="Account_headone">
                <div class="Account_div">
                    <div class="iconfont icon-shouji"></div>
                    <p>手机</p>
                    <div><van-icon name="arrow" /></div>
                </div>
            </div>
            <!-- 安全设置 -->
            <div class="Account_binding">
                <span>安全设置</span>
            </div>
            <div class="Account_headone">
                <!-- 登录密码 -->
                <div class="Account_div" @click="loginpassword()">
                    <div>登录密码</div>
                    <div :style="{color:'gray'}">修改<van-icon name="arrow" /></div>
                </div>
            </div>
            <!-- 退出登录 -->
            <div class="quit_button" @click="quit()">
                <input type="button" value="退出登录">
            </div>
        </div>
    </div>
</template>
<script>
import Header from "@/page/City/Header"
import {Dialog} from 'vant'
export default {
    components:{
        Header,
        [Dialog.Component.name]: Dialog.Component
    },
   data() {
      return {
          show:false
      }
   },
   created() {
       //this.arr=this.$store.state.ma
       this.axios.get(`http://elm.cangdu.org/v1/user?user_id=38150`).then(res=>{
           this.arr=res.data
       })
   },
   methods: {
    //    退出
    quit(){
       this.axios.get("http://elm.cangdu.org/v2/signout").then((res)=>{
        //    console.log(res)
            Dialog.confirm({
                message: '确定要退出吗'
            }).then(() => {
                this.$store.commit("tui",res.data)
                this.$router.push("/home/takeout")
            }).catch(() => {
            });
       })
    },
    // 收货地址
    ShiAddress(){
        this.$router.push("/home/ShiAddress")
    },
    // 跳转重置密码
    loginpassword(){
        this.$router.push("/home/logpass")
    },
    //上传头像
    shchuan(){
        //获取input值
        let input = this.$refs.inp;
        console.log(input.files[0])
        //存储
        let data = new FormData();
        data.append("file",input.files[0]);//类型 值
        //从新设置请求头
        let headers={headers:{"Content-Type": "multipart/form-data"}};
        this.axios.post("http://elm.cangdu.org/eus/v1/users/38184/avatar",data,headers).then(res=>{
        console.log(res)
            if(res.data.image_path != ""){
            this.$store.commit('tu',res.data.image_path);
            this.img=res.data.image_path
            } 
        })
    }
   },
   mounted() {

   }
}
</script>

<style lang="scss" scoped>
body{
    background: rgb(243, 239, 239);
}
.Account{
    width: 100%;
    margin-top: 1rem;
    .Account_head {
        font-size: 0.35rem;
        .Account_headone{
            background: white;
            margin-top: 0.15rem;
            .icon-shouji{
                color:#008AD3;
                font-size: 0.5rem;
            }
            .Account_div{
                display: flex;
                line-height: 1rem;
                border-bottom: 1px solid rgb(224, 222, 222);
                position: relative;
                >div:nth-of-type(1){
                    padding-left: 0.2rem;
                }
                >div:nth-of-type(2){
                    flex: 1;
                    text-align: right;
                    img{
                        width: 1rem;
                        height: 1rem;
                        border-radius: 0.5rem;
                    }
                }
                >div:nth-of-type(3){
                    color: gray;
                }
                .file{
                    position: absolute;
                    display: block;
                    width: 100%;
                    height: 1rem;
                    opacity: 0;
                }
            }
        }
        .Account_binding{
            margin-top: 0.15rem;
            padding-left: 0.2rem;
        }
        .quit_button{
            width: 100%;
            input{
                width: 90%;
                margin-left: 0.45rem;
                margin-top: 0.25rem;
                background: #D8584A;
                border:none;
                color: white;
                font-size: 0.45rem;
                border-radius:0.1rem;
            }
        }
    }
}
</style>


